import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, AlertController, ToastController, ActionSheetController } from 'ionic-angular';
import { AuthProvider } from '../../providers/auth/auth';
import { AddForSalePetPage }from '../add-for-sale-pet/add-for-sale-pet';
import * as firebase from 'firebase';
import 'firebase/firestore';
import { BuyPetDetailsPage } from '../buy-pet-details/buy-pet-details';
import { EditForSalePetPage } from '../edit-for-sale-pet/edit-for-sale-pet';

@IonicPage()
@Component({
  selector: 'page-admin-buy-and-sell',
  templateUrl: 'admin-buy-and-sell.html',
})
export class AdminBuyAndSellPage {
  pets: any;
  petsCount: number = 0;
  db: any = firebase.firestore();
  pageLoaded: boolean = false;
  userId: string = localStorage.getItem('userId');

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams, 
    public authProvider: AuthProvider,
    public modalCtrl: ModalController,
    public alertCtrl: AlertController,
    public toastCtrl: ToastController,
    public actionSheetCtrl: ActionSheetController
  ) {
    this.getItems();
  }

  onSearch(ev: any) {
    this.pageLoaded = false;
    let val = ev.target.value;

    this.db.collection('buypets').where("isactive", "==", true).onSnapshot(snapshots => {
      console.log('snapshots', snapshots);
      let pets = [];
      snapshots.forEach(doc => {
        let docData = doc.data();
        let interestedUsers = docData['interestedUsers'];
     
        if(interestedUsers.indexOf(this.userId) == -1) { // no user records
          docData['interested'] = false;
        }else {
          docData['interested'] = true;
        }
        
        docData['buyPetId'] = doc.id
        pets.push(docData);
      });

      this.pets = pets;
      this.petsCount = Object.keys(pets).length;

      if (val && val.trim() != '') {
        this.pets =  this.pets.filter((el) => {
          return  (el.name.toLowerCase().indexOf(val.toLowerCase()) > -1) ||
                  (el.breed.toLowerCase().indexOf(val.toLowerCase()) > -1)
        });
      }

      setTimeout(() => {
        this.pageLoaded = true;
      }, 700);
    }),(err => {
      console.log('err', err);
    });

   

  }

  onCancel(ev: any) {
    this.getItems();
  }

  getItems() {
    this.db.collection('buypets').where("isactive", "==", true).onSnapshot(snapshots => {
      console.log('snapshots', snapshots);
      let pets = [];
      snapshots.forEach(doc => {
        let docData = doc.data();
        let interestedUsers = docData['interestedUsers'];
     
        if(interestedUsers.indexOf(this.userId) == -1) { // no user records
          docData['interested'] = false;
        }else {
          docData['interested'] = true;
        }
        
        docData['buyPetId'] = doc.id
        pets.push(docData);
      });
      this.pets = pets;
      console.log('this.pets', this.pets);
      this.petsCount = Object.keys(pets).length;
      this.pageLoaded = true;
    }),(err => {
      console.log('err', err);
    });
  }

  addForSalePet() {
    let modal = this.modalCtrl.create(AddForSalePetPage);

    modal.onDidDismiss(data => {
      
    });

    modal.present();
  }

  removePost(buyPetId) {
    let confirm = this.alertCtrl.create({
      title: 'Your pet was sold?',
      message: 'Are you sure do you want to remove your post?',
      buttons: [
        {
          text: 'Cancel',
          handler: () => {
          }
        },
        {
          text: 'Ok',
          handler: () => {
            this.db.collection('buypets').doc(buyPetId).update({
              isactive: false
            });

            let toast = this.toastCtrl.create({
              message: 'Post was removed',
              duration: 5000,
              position: 'bottom'
            });
      
            toast.present();
          }
        }
      ]
    });
    confirm.present();
  }

  action(petId) {
    console.log('petId', petId);
    let actionSheet = this.actionSheetCtrl.create({
      title: 'Modify your post',
      buttons: [
        {
          text: 'Purchase',
          icon: 'cart',
          handler: () => {
            this.removePost(petId);
          }
        },{
          text: 'Edit',
          icon: 'create',
          handler: () => {
            let data: object = {
              petId: petId
            };
            let modal = this.modalCtrl.create(EditForSalePetPage, data);
          
            modal.onDidDismiss(data => {
              
            });
        
            modal.present();
          }
        },{
          text: 'Cancel',
          icon: 'close',
          handler: () => {
            console.log('Cancel clicked');
          }
        }
      ]
    });
    actionSheet.present();
  }

  buyPetDetails(pet) {
    console.log('pet', pet);
    let modal = this.modalCtrl.create(BuyPetDetailsPage, pet);

    modal.onDidDismiss(data => {
      if (data) {
        // this.loadProfile();
      }
    });

    modal.present();
  }

  thumbsUp(petId) {
    let petRef = this.db.collection('buypets').doc(petId);
    petRef.get().then(pet => {
      let data = pet.data();
      let interestedUsers = data['interestedUsers'];
      
        if(interestedUsers.indexOf(this.userId) == -1) { // no user records
          interestedUsers.push(this.userId)
          petRef.update({
            interestedUsers: interestedUsers
          });

          let toast = this.toastCtrl.create({
            message: 'Interested!',
            duration: 5000,
            position: 'bottom'
          });
    
          toast.present();
        }else {
          interestedUsers.splice(this.userId, 1);
          petRef.update({
            interestedUsers: interestedUsers
          });

          let toast = this.toastCtrl.create({
            message: 'Not interested!',
            duration: 5000,
            position: 'bottom'
          });
    
          toast.present();
        }

        petRef.update({
          interestedCount: interestedUsers.length
        });
     
    }).catch(err => {
      console.log('err', err);
    });
  }

}



